/* --- ESTILOS ESPECÍFICOS PARA MENSAJES PREDETERMINADOS --- */

/* Margen superior de 300px y padding inferior para la sección de mensajes predeterminados */
.predefined-messages-container-margin {
    padding: 200px 100px 100px 100px; /* top, right, bottom, left */
}

/* Contenedor principal para el layout de dos columnas */
.predefined-messages-layout {
    display: flex;
    flex-wrap: wrap; /* Permite que las columnas se apilen en pantallas pequeñas */
    gap: 20px; /* Espacio entre las columnas */
    margin-top: 20px;
    width: 100%;
    text-align: left; /* Asegura que el texto dentro de este layout se alinee a la izquierda por defecto */
}

.predefined-messages-add,
.predefined-messages-saved {
    flex: 1; /* Ocupa el espacio disponible */
    min-width: 280px; /* Ancho mínimo antes de apilarse */
    display: flex;
    flex-direction: column;
}

/* Estilo para los grupos de formulario */
.form-group {
    margin-bottom: 15px;
    text-align: left; /* Alineación del texto dentro del form-group */
    width: 100%;
}

.form-group label {
    display: block;
    margin-bottom: 5px;
    font-weight: bold;
    color: var(--color-text-dark);
}

/* Estilos para inputs de texto y textareas */
.form-group input[type="text"],
.form-group textarea {
    width: 100%;
    padding: 10px;
    border: 1px solid var(--color-accent1);
    border-radius: 4px;
    font-size: 1em;
    box-sizing: border-box;
    background-color: #fff;
    color: var(--color-text-dark);
    text-align: left; /* Mantener el texto alineado a la izquierda dentro del input/textarea */
}

.form-group textarea {
    resize: vertical;
}

/* Estilo para el input de palabras clave en foco */
#keywordGroupInput:focus {
    border-color: var(--color-primary);
    box-shadow: 0 0 0 3px rgba(var(--color-primary-rgb), 0.2);
    outline: none;
}

/* Contenedor para las etiquetas de palabras clave (requiere JS) */
.keyword-input-container {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    border: 1px solid var(--color-accent1);
    border-radius: 4px;
    padding: 5px;
    background-color: #fff;
    min-height: 40px; /* Altura mínima para el contenedor */
    box-sizing: border-box;
}

/* Estilo para una posible "tag" de palabra clave (requiere JS para envolver cada palabra) */
.keyword-tag {
    display: inline-flex; /* Usar flex para centrar el contenido y el botón de cerrar */
    align-items: center;
    background-color: var(--color-accent1);
    color: var(--color-primary);
    padding: 4px 8px;
    border-radius: 4px;
    margin-right: 5px;
    margin-bottom: 5px;
    font-size: 0.85em;
    font-weight: 600;
    white-space: nowrap; /* Evita que el texto de la etiqueta se rompa */
    font-style: italic; /* Hacer el texto dentro de la etiqueta cursiva */
}

.keyword-tag .remove-tag {
    background: none;
    border: none;
    color: var(--color-primary);
    font-size: 1em;
    margin-left: 5px;
    cursor: pointer;
    line-height: 1;
    padding: 0;
    transition: color 0.2s ease;
}

.keyword-tag .remove-tag:hover {
    color: #dc3545; /* Color rojo para el botón de cerrar en hover */
}

/* El input dentro del contenedor de etiquetas */
.keyword-input-container input[type="text"] {
    flex-grow: 1; /* Ocupa el espacio restante */
    border: none;
    padding: 5px;
    margin-bottom: 0; /* Eliminar margen inferior para que se alinee con las tags */
    min-width: 80px; /* Ancho mínimo para el input de escritura */
    background-color: transparent; /* Fondo transparente para que el contenedor lo defina */
}

.keyword-input-container input[type="text"]:focus {
    outline: none;
    box-shadow: none;
}


/* Sección de subida de archivos */
.file-upload-section {
    margin-top: 20px;
    margin-bottom: 20px;
    text-align: left; /* Alineación del texto dentro de la sección de subida de archivos */
    width: 100%;
    border: 1px dashed var(--color-accent1);
    padding: 15px;
    border-radius: 8px;
}

.file-upload-section p {
    margin-bottom: 10px;
    font-weight: bold;
    color: var(--color-text-dark);
}

.file-upload-section input[type="file"] {
    width: 100%;
    padding: 8px;
    border: 1px solid var(--color-accent2);
    border-radius: 4px;
    background-color: var(--color-bg-light);
    cursor: pointer;
}

#uploadedFilesList {
    list-style: none;
    padding: 0;
    margin-top: 15px;
}

#uploadedFilesList li {
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: var(--color-accent2);
    padding: 8px 12px;
    border-radius: 5px;
    margin-bottom: 8px;
    font-size: 0.9em;
    color: var(--color-text-dark);
    text-align: left; /* Asegurar que el texto dentro del li se alinee a la izquierda */
}

#uploadedFilesList li .file-name {
    flex-grow: 1;
    margin-right: 10px;
    word-break: break-all;
}

#uploadedFilesList li .remove-file-btn {
    border-radius: 50%;
    width: 24px;
    height: 24px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 0.8em;
    background-color: #dc3545; /* Color rojo para el botón de eliminar */
    color: white;
    cursor: pointer;
    transition: background-color 0.2s ease;
}

#uploadedFilesList li .remove-file-btn:hover {
    background-color: #c82333;
}

/* Estilos para el botón de guardar mensaje predeterminado */
#addPredeterminedMessageBtn {
    width: auto;
    padding: 10px 25px;
    margin-top: 15px; /* Mantener un margen superior para separación del contenido */
    background-color: var(--color-secondary);
    color: var(--color-primary);
    box-shadow: 0 2px 5px rgba(var(--color-secondary-rgb), 0.3);
    /* Ya no es fijo, se comporta como un elemento de flujo normal */
}

#addPredeterminedMessageBtn:hover {
    background-color: #0cb982;
    box-shadow: 0 4px 8px rgba(var(--color-secondary-rgb), 0.4);
}


/* Estilos para los grupos predeterminados guardados */
#savedPredeterminedGroups {
    margin-top: 20px;
    text-align: left; /* Mantener la alineación a la izquierda para la lista de grupos */
    width: 100%;
    max-height: 350px; /* Altura máxima para el scroll */
    overflow-y: auto; /* Habilitar scroll vertical */
    padding-right: 10px; /* Espacio para la barra de scroll */
}

.predetermined-group-item {
    background-color: #fff;
    border: 1px solid var(--color-accent1);
    border-radius: 8px;
    padding: 15px;
    margin-bottom: 15px;
    box-shadow: 0 2px 5px rgba(var(--color-text-dark-rgb), 0.05);
}

.predetermined-group-item h4 {
    color: var(--color-primary);
    margin-top: 0;
    margin-bottom: 10px;
    font-size: 1.1em;
}

.predetermined-group-item p {
    margin-bottom: 5px;
    font-size: 0.9em;
    color: var(--color-text-gray);
}

.predetermined-group-item ul {
    list-style: disc;
    margin-left: 20px;
    margin-top: 10px;
    padding: 0;
}

.predetermined-group-item ul li {
    font-size: 0.9em;
    color: var(--color-text-gray);
    margin-bottom: 3px;
}

.predetermined-group-item .group-actions {
    margin-top: 15px;
    text-align: right;
}

.predetermined-group-item .group-actions button {
    padding: 8px 15px;
    margin-left: 10px;
    font-size: 0.9em;
}

.predetermined-group-item .group-actions .delete-group-btn {
    background-color: #dc3545;
    color: white;
}
.predetermined-group-item .group-actions .delete-group-btn:hover {
    background-color: #c82333;
}

/* Clase para las palabras clave en cursiva */
.keyword-italic {
    font-style: italic;
    color: var(--color-primary); /* Puedes ajustar el color si quieres que resalte más */
    font-weight: 500; /* Para darle un poco más de peso sin ser negrita */
}

/* Media query para pantallas pequeñas (apilar columnas) */
@media (max-width: 768px) {
    .predefined-messages-container-margin {
        margin-top: 0; /* Eliminar margen superior en móviles si se apilan */
        padding-bottom: 100px; /* Ajustar padding inferior para móviles */
    }

    .predefined-messages-layout {
        flex-direction: column;
    }

    .predefined-messages-add,
    .predefined-messages-saved {
        min-width: 100%; /* Ocupa todo el ancho en pantallas pequeñas */
    }

    #savedPredeterminedGroups {
        max-height: 250px; /* Ajustar altura para móviles si es necesario */
    }

    #addPredeterminedMessageBtn {
        position: static; /* Quitar el fixed en móviles si no se desea */
        width: 100%;
        transform: none;
        margin-top: 20px; /* Añadir margen superior si se convierte en estático */
    }
}
