/* src/components/Agenda.css */

.agenda-container {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  padding: 20px;
  box-sizing: border-box;
}

.coming-soon-card {
  max-width: 400px;
  width: 100%;
  padding: 40px 30px;
  text-align: center;
  background-color: #f8f9fa;
  border: 1px solid #e9ecef;
  border-radius: 12px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.05);
}

.icon-wrapper {
  margin-bottom: 20px;
  color: var(--color-primary);
}

.coming-soon-card h2 {
  color: var(--color-text-dark);
  font-size: 1.5em;
  margin-bottom: 10px;
}

.coming-soon-card p {
  color: var(--color-text-gray);
  font-size: 1em;
  margin: 0;
}