/* Backdrop del modal */
.modal-backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(26, 32, 44, 0.8); /* Usando el gris oscuro con transparencia */
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  opacity: 0;
  transition: opacity 0.3s ease;
}

.modal-backdrop.show {
  opacity: 1;
}

/* Contenido del modal */
.modal-content {
  background-color: #f8f9fa; /* Blanco ligeramente roto */
  border-radius: 12px;
  padding: 32px;
  min-width: 400px;
  max-width: 500px;
  box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04);
  transform: scale(0.95);
  transition: transform 0.3s ease;
}

.modal-backdrop.show .modal-content {
  transform: scale(1);
}

/* Título del modal */
.modal-content h2 {
  color: #000191; /* Azul oscuro principal */
  font-size: 24px;
  font-weight: 600;
  margin: 0 0 24px 0;
  text-align: center;
}


.modal-notification {
  padding: 10px 15px;
  background-color: #fff3cd; /* Un amarillo suave de advertencia */
  color: #000191;          /* Texto oscuro para buen contraste */
  border: 1px solid #ffecb5;
  border-radius: 5px;
  text-align: center;
  margin-bottom: 15px;      /* Espacio antes del selector */
  animation: fadeIn 0.5s ease-in-out;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(-10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Error del modal */
.modal-error {
  background-color: #fee;
  color: #c53030;
  padding: 12px 16px;
  border-radius: 8px;
  margin-bottom: 16px;
  font-size: 14px;
  border-left: 4px solid #fc8181;
  animation: slideIn 0.3s ease;
}

/* Select del modal */
.modal-content select {
  width: 100%;
  padding: 12px;
  margin-bottom: 20px;
  border: 2px solid #c5dcf0; /* Azul muy pálido */
  border-radius: 8px;
  background-color: #fff;
  color: #1a202c; /* Gris muy oscuro */
  font-size: 16px;
  transition: border-color 0.3s ease;
  cursor: pointer;
}

.modal-content select:hover {
  border-color: #adc9e7; /* Azul pálido */
}

.modal-content select:focus {
  outline: none;
  border-color: #000191; /* Azul oscuro */
  box-shadow: 0 0 0 3px rgba(0, 1, 145, 0.1);
}

/* Textarea del modal */
.modal-content textarea {
  width: 100%;
  min-height: 200px;
  padding: 12px;
  border: 2px solid #c5dcf0; /* Azul muy pálido */
  border-radius: 8px;
  font-size: 14px;
  font-family: 'Consolas', 'Monaco', 'Courier New', monospace;
  resize: vertical;
  transition: border-color 0.3s ease;
  color: #1a202c; /* Gris muy oscuro */
  background-color: #fff;
}

.modal-content textarea:focus {
  outline: none;
  border-color: #000191; /* Azul oscuro */
  box-shadow: 0 0 0 3px rgba(0, 1, 145, 0.1);
}

.modal-content textarea:disabled {
  background-color: #e7effc; /* Azul muy pálido */
  color: #4a5568; /* Gris medio */
  cursor: not-allowed;
}

/* Input del modal */
.modal-content input[type="text"] {
  width: 100%;
  padding: 12px;
  border: 2px solid #c5dcf0; /* Azul muy pálido */
  border-radius: 8px;
  font-size: 16px;
  transition: border-color 0.3s ease;
  color: #1a202c; /* Gris muy oscuro */
  background-color: #fff;
}

.modal-content input[type="text"]:focus {
  outline: none;
  border-color: #000191; /* Azul oscuro */
  box-shadow: 0 0 0 3px rgba(0, 1, 145, 0.1);
}

.modal-content input[type="text"]:disabled {
  background-color: #e7effc; /* Azul muy pálido */
  color: #4a5568; /* Gris medio */
  cursor: not-allowed;
}

/* Descripción de key */
#keyDescription {
  margin: 16px 0;
  font-size: 14px;
  color: #4a5568; /* Gris medio */
  line-height: 1.5;
}

#keyDescription a {
  color: #000191; /* Azul oscuro */
  text-decoration: none;
  font-weight: 500;
  transition: color 0.3s ease;
}

#keyDescription a:hover {
  color: #0eda91; /* Verde claro */
  text-decoration: underline;
}

/* Párrafos del modal */
.modal-content p {
  color: #4a5568; /* Gris medio */
  font-size: 16px;
  line-height: 1.6;
  margin: 16px 0;
  text-align: center;
}

/* Contenedor de botones */
.modal-buttons {
  display: flex;
  gap: 12px;
  justify-content: center;
  margin-top: 24px;
}

/* Botones base */
.modal-buttons button {
  padding: 12px 24px;
  border: none;
  border-radius: 8px;
  font-size: 16px;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.3s ease;
  min-width: 100px;
}

.modal-buttons button:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

/* Botón guardar */
.save-button {
  background-color: #0eda91; /* Verde claro */
  color: #fff;
}

.save-button:hover:not(:disabled) {
  background-color: #0bc47d;
  transform: translateY(-2px);
  box-shadow: 0 4px 6px rgba(14, 218, 145, 0.3);
}

.save-button:active:not(:disabled) {
  transform: translateY(0);
}

/* Botón cerrar */
.close-button {
  background-color: #adc9e7; /* Azul pálido */
  color: #1a202c; /* Gris muy oscuro */
}

.close-button:hover {
  background-color: #9bb9db;
  transform: translateY(-2px);
  box-shadow: 0 4px 6px rgba(173, 201, 231, 0.3);
}

.close-button:active {
  transform: translateY(0);
}

/* Botón descargar */
.download-button {
  background-color: #000191; /* Azul oscuro */
  color: #fff;
}

.download-button:hover {
  background-color: #000175;
  transform: translateY(-2px);
  box-shadow: 0 4px 6px rgba(0, 1, 145, 0.3);
}

.download-button:active {
  transform: translateY(0);
}

/* Botón OK */
.ok-button {
  background-color: #0eda91; /* Verde claro */
  color: #fff;
}

.ok-button:hover {
  background-color: #0bc47d;
  transform: translateY(-2px);
  box-shadow: 0 4px 6px rgba(14, 218, 145, 0.3);
}

.ok-button:active {
  transform: translateY(0);
}

/* Botón continuar (modal de reinicio) */
.modal-buttons button:only-child {
  background-color: #000191; /* Azul oscuro */
  color: #fff;
}

.modal-buttons button:only-child:hover {
  background-color: #000175;
  transform: translateY(-2px);
  box-shadow: 0 4px 6px rgba(0, 1, 145, 0.3);
}

/* Títulos específicos del modal de versión */
#versionModalTitle {
  color: #000191; /* Azul oscuro */
}

#versionModalMessage {
  color: #4a5568; /* Gris medio */
}

/* Animaciones */
@keyframes slideIn {
  from {
    opacity: 0;
    transform: translateY(-10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Responsive */
@media (max-width: 600px) {
  .modal-content {
    min-width: 90%;
    max-width: 90%;
    padding: 24px;
  }

  .modal-content h2 {
    font-size: 20px;
  }

  .modal-buttons {
    flex-direction: column;
    width: 100%;
  }

  .modal-buttons button {
    width: 100%;
  }
}