/* Contenedor principal */
.cuenta-container {
  min-height: 100vh;
  background-color: var(--color-bg-light);
  padding: 300px 10% 50px;
}

.cuenta-content {
  max-width: 768px;
  margin: 0 auto;
}

/* Header */
.cuenta-header {
  text-align: center;
  margin-bottom: 2rem;
}

.cuenta-header-icon {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 4rem;
  height: 4rem;
  border-radius: 50%;
  background-color: var(--color-accent2);
  margin-bottom: 1rem;
}

.cuenta-icon {
  font-size: 1.5rem;
  color: var(--color-primary);
}

.cuenta-title {
  font-size: 1.875rem;
  font-weight: 700;
  color: var(--color-text-dark);
  margin-bottom: 0.5rem;
}

.cuenta-subtitle {
  color: var(--color-text-gray);
}

/* Card principal */
.cuenta-card {
  background-color: white;
  border-radius: 0.75rem;
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
  overflow: hidden;
}

/* Estados de carga */
.cuenta-loading {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 4rem 0;
}

.cuenta-spinner {
  width: 2.5rem;
  height: 2.5rem;
  border: 2px solid transparent;
  border-top: 2px solid var(--color-primary);
  border-radius: 50%;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  from { transform: rotate(0deg); }
  to { transform: rotate(360deg); }
}

.cuenta-loading-text {
  margin-left: 0.75rem;
  color: var(--color-text-gray);
}

/* Error */
.cuenta-error {
  margin: 1.5rem;
  background-color: #fef2f2;
  border: 1px solid #fecaca;
  border-radius: 0.5rem;
  padding: 1rem;
}

.cuenta-error-text {
  color: #dc2626;
  margin-bottom: 0.5rem;
}

.cuenta-error-retry {
  color: #b91c1c;
  font-weight: 500;
  background: none;
  border: none;
  cursor: pointer;
  transition: color var(--transition-speed) var(--transition-ease);
}

.cuenta-error-retry:hover {
  color: #991b1b;
}

/* Información de la cuenta */
.cuenta-info {
  padding: 2rem;
}

.cuenta-data {
  margin-bottom: 2rem;
}

.cuenta-section-title {
  font-size: 1.25rem;
  font-weight: 600;
  color: var(--color-text-dark);
  margin-bottom: 1rem;
}

/* Items de información */
.cuenta-item {
  display: flex;
  align-items: flex-start;
  gap: 1rem;
  padding: 1rem;
  border-radius: 0.5rem;
  background-color: var(--color-bg-light);
  margin-bottom: 1.5rem;
}

.cuenta-item-icon {
  font-size: 1.5rem;
  flex-shrink: 0;
}

.cuenta-item-content {
  flex: 1;
}

.cuenta-item-title {
  font-weight: 500;
  color: var(--color-text-dark);
  margin-bottom: 0.25rem;
}

.cuenta-item-value {
  color: var(--color-text-gray);
}

.cuenta-item-capitalize {
  text-transform: capitalize;
}

/* Estado del plan */
.cuenta-status {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  margin-bottom: 0.5rem;
}

.cuenta-status-dot {
  width: 0.75rem;
  height: 0.75rem;
  border-radius: 50%;
  display: inline-block;
}

.cuenta-status-active {
  background-color: #10b981;
}

.cuenta-status-inactive {
  background-color: #ef4444;
}

.cuenta-status-text {
  font-weight: 500;
}

.cuenta-status-active-text {
  color: #047857;
}

.cuenta-status-inactive-text {
  color: #dc2626;
}

.cuenta-status-date {
  font-size: 0.875rem;
  color: var(--color-text-gray);
}

/* Botones de acción */
.cuenta-actions {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.cuenta-btn {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.75rem;
  padding: 1rem 1.5rem;
  font-weight: 500;
  border-radius: 0.5rem;
  cursor: pointer;
  transition: all var(--transition-speed) var(--transition-ease);
  border: none;
  font-size: 1rem;
}

.cuenta-btn-icon {
  font-size: 1.125rem;
}

.cuenta-btn-extend {
  background-color: var(--color-secondary);
  color: white;
  box-shadow: 0 2px 4px rgba(var(--color-secondary-rgb), 0.2);
}

.cuenta-btn-extend:hover {
  transform: translateY(-1px);
  box-shadow: 0 6px 12px rgba(var(--color-secondary-rgb), 0.3);
}

.cuenta-btn-logout {
  background-color: white;
  color: var(--color-primary);
  border: 2px solid var(--color-primary);
}

.cuenta-btn-logout:hover {
  background-color: var(--color-bg-light);
}

/* Modal de confirmación */
.cuenta-modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 50;
  padding: 1rem;
}

.cuenta-modal {
  background-color: white;
  border-radius: 0.5rem;
  box-shadow: 0 25px 50px -12px rgba(0, 0, 0, 0.25);
  width: 100%;
  max-width: 28rem;
}

.cuenta-modal-content {
  padding: 1.5rem;
}

.cuenta-modal-header {
  text-align: center;
  margin-bottom: 1.5rem;
}

.cuenta-modal-warning-icon {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 4rem;
  height: 4rem;
  border-radius: 50%;
  background-color: #fef2f2;
  margin: 0 auto 1rem;
}

.cuenta-modal-warning-icon span {
  font-size: 1.875rem;
}

.cuenta-modal-title {
  font-size: 1.125rem;
  font-weight: 600;
  color: var(--color-text-dark);
  margin-bottom: 0.5rem;
}

.cuenta-modal-description {
  color: var(--color-text-gray);
}

.cuenta-modal-actions {
  display: flex;
  gap: 0.75rem;
}

.cuenta-modal-btn {
  flex: 1;
  padding: 0.5rem 1rem;
  border-radius: 0.5rem;
  font-weight: 500;
  cursor: pointer;
  transition: all var(--transition-speed) var(--transition-ease);
  border: none;
}

.cuenta-modal-btn-cancel {
  background-color: white;
  color: var(--color-text-gray);
  border: 1px solid #d1d5db;
}

.cuenta-modal-btn-cancel:hover {
  background-color: var(--color-bg-light);
}

.cuenta-modal-btn-confirm {
  background-color: var(--color-primary);
  color: white;
}

.cuenta-modal-btn-confirm:hover {
  opacity: 0.9;
}

/* Responsive */
@media (max-width: 768px) {
  .cuenta-container {
    padding: 100px 5% 25px;
  }
  
  .cuenta-title {
    font-size: 1.5rem;
  }
  
  .cuenta-info {
    padding: 1.5rem;
  }
  
  .cuenta-item {
    padding: 0.75rem;
  }
}