/* src/components/MensajeInicial.css */

.mensaje-inicial-container {
    padding: 200px 10% 20px 10%;
    max-width: 1200px;
    margin: 0 auto;
    background-color: var(--color-bg-light);
    min-height: 100vh;
    box-sizing: border-box;
}

.mensaje-inicial-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 30px;
    padding-bottom: 20px;
    border-bottom: 2px solid var(--color-accent2);
}

.mensaje-inicial-header h1 {
    color: var(--color-primary);
    margin: 0;
    font-size: 2.5rem;
    font-weight: 700;
}

.agente-info {
    display: flex;
    align-items: center;
    background: linear-gradient(135deg, var(--color-primary), var(--color-secondary));
    padding: 12px 20px;
    border-radius: 12px;
    color: white;
    font-weight: 600;
    box-shadow: 0 4px 12px rgba(var(--color-primary-rgb), 0.3);
}

.agente-label {
    margin-right: 8px;
    opacity: 0.9;
}

.agente-numero {
    background: rgba(255, 255, 255, 0.2);
    padding: 4px 12px;
    border-radius: 20px;
    font-weight: 700;
    font-size: 1.1rem;
}

/* Mensajes de notificación */
.mensaje-notificacion {
    padding: 15px 20px;
    border-radius: 8px;
    margin-bottom: 20px;
    font-weight: 500;
    animation: slideInDown 0.3s ease;
}

.mensaje-notificacion.success {
    background-color: #d4edda;
    color: #155724;
    border: 1px solid #c3e6cb;
}

.mensaje-notificacion.error {
    background-color: #f8d7da;
    color: #721c24;
    border: 1px solid #f5c6cb;
}

.mensaje-notificacion.warning {
    background-color: #fff3cd;
    color: #856404;
    border: 1px solid #ffeaa7;
}

/* Loading spinner */
.loading-spinner {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 300px;
    color: var(--color-text-gray);
}

.spinner {
    width: 40px;
    height: 40px;
    border: 4px solid var(--color-accent2);
    border-top: 4px solid var(--color-primary);
    border-radius: 50%;
    animation: spin 1s linear infinite;
    margin-bottom: 15px;
}

@keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
}

/* Contenido principal */
.mensaje-inicial-content {
    display: flex;
    flex-direction: column;
    gap: 30px;
}

/* Secciones */
.configuracion-section,
.mensaje-section,
.archivos-section,
.acciones-section,
.info-section {
    background: white;
    padding: 25px;
    border-radius: 15px;
    box-shadow: 0 4px 12px rgba(var(--color-text-dark-rgb), 0.08);
    border: 1px solid var(--color-accent2);
}

.section-label {
    display: block;
    font-weight: 600;
    color: var(--color-text-dark);
    margin-bottom: 15px;
    font-size: 1.1rem;
}

/* Switch toggle */
.switch-container {
    display: flex;
    align-items: center;
    gap: 15px;
}

.switch {
    position: relative;
    display: inline-block;
    width: 60px;
    height: 34px;
}

.switch input {
    opacity: 0;
    width: 0;
    height: 0;
}

.slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #ccc;
    transition: .4s;
    border-radius: 34px;
}

.slider:before {
    position: absolute;
    content: "";
    height: 26px;
    width: 26px;
    left: 4px;
    bottom: 4px;
    background-color: white;
    transition: .4s;
    border-radius: 50%;
}

input:checked + .slider {
    background-color: var(--color-secondary);
}

input:checked + .slider:before {
    transform: translateX(26px);
}

.switch-label {
    font-weight: 600;
    color: var(--color-text-dark);
    font-size: 1.1rem;
}

/* Textarea */
.mensaje-textarea {
    width: 100%;
    padding: 15px;
    border: 2px solid var(--color-accent2);
    border-radius: 12px;
    font-size: 1rem;
    font-family: inherit;
    resize: vertical;
    min-height: 120px;
    transition: border-color 0.3s ease, box-shadow 0.3s ease;
}

.mensaje-textarea:focus {
    outline: none;
    border-color: var(--color-primary);
    box-shadow: 0 0 0 3px rgba(var(--color-primary-rgb), 0.1);
}

.mensaje-textarea::placeholder {
    color: var(--color-text-gray);
}

/* Archivos */
.archivos-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 20px;
}

.agregar-archivos-btn {
    background: linear-gradient(135deg, var(--color-primary), var(--color-secondary));
    color: white;
    border: none;
    padding: 12px 20px;
    border-radius: 8px;
    cursor: pointer;
    font-weight: 600;
    transition: all 0.3s ease;
    box-shadow: 0 4px 12px rgba(var(--color-primary-rgb), 0.3);
}

.agregar-archivos-btn:hover {
    transform: translateY(-2px);
    box-shadow: 0 6px 20px rgba(var(--color-primary-rgb), 0.4);
}

.agregar-archivos-btn:active {
    transform: translateY(0);
}

.archivos-contador {
    color: var(--color-text-gray);
    font-weight: 500;
    margin-bottom: 15px;
    padding: 10px;
    background: var(--color-accent2);
    border-radius: 6px;
    text-align: center;
}

.archivos-grid {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
    gap: 12px;
}

.archivo-item {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 12px 15px;
    background: var(--color-bg-light);
    border: 1px solid var(--color-accent2);
    border-radius: 8px;
    transition: all 0.3s ease;
}

.archivo-item:hover {
    background: white;
    border-color: var(--color-primary);
    transform: translateY(-1px);
    box-shadow: 0 4px 12px rgba(var(--color-primary-rgb), 0.1);
}

.archivo-info {
    display: flex;
    align-items: center;
    gap: 10px;
    flex: 1;
    min-width: 0;
}

.archivo-icono {
    font-size: 1.5rem;
    flex-shrink: 0;
}

.archivo-nombre {
    font-weight: 500;
    color: var(--color-text-dark);
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

.eliminar-archivo-btn {
    background: #ff4757;
    color: white;
    border: none;
    width: 28px;
    height: 28px;
    border-radius: 50%;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 14px;
    font-weight: 700;
    transition: all 0.3s ease;
    flex-shrink: 0;
}

.eliminar-archivo-btn:hover {
    background: #ff3742;
    transform: scale(1.1);
}

/* Botón guardar */
.guardar-btn {
    background: linear-gradient(135deg, var(--color-secondary), var(--color-primary));
    color: white;
    border: none;
    padding: 15px 30px;
    border-radius: 12px;
    font-size: 1.1rem;
    font-weight: 600;
    cursor: pointer;
    transition: all 0.3s ease;
    box-shadow: 0 4px 15px rgba(var(--color-secondary-rgb), 0.3);
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 10px;
    min-height: 50px;
}

.guardar-btn:hover:not(:disabled) {
    transform: translateY(-2px);
    box-shadow: 0 6px 25px rgba(var(--color-secondary-rgb), 0.4);
}

.guardar-btn:active:not(:disabled) {
    transform: translateY(0);
}

.guardar-btn:disabled {
    opacity: 0.7;
    cursor: not-allowed;
}

.btn-spinner {
    width: 20px;
    height: 20px;
    border: 2px solid rgba(255, 255, 255, 0.3);
    border-top: 2px solid white;
    border-radius: 50%;
    animation: spin 1s linear infinite;
}

/* Info box */
.info-box {
    background: linear-gradient(135deg, var(--color-accent1), var(--color-accent2));
    padding: 20px;
    border-radius: 12px;
    border-left: 4px solid var(--color-primary);
}

.info-box h3 {
    color: var(--color-primary);
    margin: 0 0 15px 0;
    font-size: 1.2rem;
}

.info-box ul {
    margin: 0;
    padding-left: 20px;
    color: var(--color-text-dark);
}

.info-box li {
    margin-bottom: 8px;
    line-height: 1.5;
}

/* Animaciones */
@keyframes slideInDown {
    from {
        transform: translateY(-20px);
        opacity: 0;
    }
    to {
        transform: translateY(0);
        opacity: 1;
    }
}

/* Responsive */
@media (max-width: 768px) {
    .mensaje-inicial-container {
        padding: 250px 5% 15px 5%;
    }

    .mensaje-inicial-header {
        flex-direction: column;
        gap: 15px;
        text-align: center;
    }

    .mensaje-inicial-header h1 {
        font-size: 2rem;
    }

    .archivos-header {
        flex-direction: column;
        align-items: stretch;
        gap: 15px;
    }

    .archivos-grid {
        grid-template-columns: 1fr;
    }

    .configuracion-section,
    .mensaje-section,
    .archivos-section,
    .acciones-section,
    .info-section {
        padding: 20px;
    }
}