

:root {
    --color-primary: #000191; /* Azul oscuro */
    --color-secondary: #0eda91; /* Verde claro */
    --color-accent1: #adc9e7; /* Azul pálido */
    --color-accent2: #c5dcf0; /* Azul muy pálido */
    --color-bg-light: #f8f9fa; /* Blanco ligeramente roto */
    --color-text-dark: #1a202c; /* Gris muy oscuro */
    --color-text-gray: #4a5568; /* Gris medio */
    --color-benefits-bg: #e7effc; /* Azul muy pálido (aunque no se usa directamente aquí) */

    /* Versiones RGB para sombras */
    --color-primary-rgb: 0, 1, 145;
    --color-secondary-rgb: 14, 218, 145;
    /* Añadido RGB para color-text-dark para sombras */
    --color-text-dark-rgb: 26, 32, 44;

    /* Propiedades comunes para transiciones */
    --transition-speed: 0.3s;
    --transition-ease: ease;
    --transition-fast: 0.1s;
}

/* Estilo cuerpo - Aplicando paleta y tipografía */
body {
    font-family: 'Inter', sans-serif; /* Aplicar tipografía */
    background-color: var(--color-bg-light); /* Usar color de fondo de la paleta */
    color: var(--color-text-dark); /* Usar color de texto principal */
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    overflow: hidden;
}

.app-wrapper {
    display: flex;
    width: 100vw;
    height: 100vh;
    overflow: hidden;
}

#splash-screen {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: var(--color-bg-light);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 9999;
    transition: opacity var(--transition-speed) var(--transition-ease), visibility var(--transition-speed) var(--transition-ease);
}

#splash-screen.hidden {
    opacity: 0;
    visibility: hidden;
}

#splash-screen img {
    max-width: 80%;
    max-height: 80%;
    object-fit: contain;
}

.sidebar {
    width: 250px;
    background-color: var(--color-primary);
    color: white;
    position: fixed;
    height: 100%;
    left: 0;
    top: 0;
    transform: translateX(-100%);
    transition: transform var(--transition-speed) var(--transition-ease);
    z-index: 100;
    padding-top: 60px;
    box-shadow: 2px 0 5px rgba(var(--color-text-dark-rgb), 0.2);
    display: flex;
    flex-direction: column;
}

.sidebar.open {
    transform: translateX(0);
}

.close-sidebar-btn {
    position: absolute;
    top: 10px;
    right: 10px;
    background: none;
    border: none;
    font-size: 2em;
    color: white;
    cursor: pointer;
    padding: 5px;
    line-height: 1;
    transition: color 0.2s ease;
}

.close-sidebar-btn:hover {
    color: var(--color-accent1);
}

/* Reestructuración del sidebar menu */
.sidebar-menu {
    display: flex;
    flex-direction: column;
    height: calc(100% - 60px);
    padding: 20px 0;
}

.menu-items-top {
    flex: 1;
    display: flex;
    flex-direction: column;
    overflow-y: auto;
}

.menu-items-bottom {
    margin-top: auto;
    padding-top: 20px;
    border-top: 1px solid rgba(255, 255, 255, 0.2);
}

.sidebar-menu .menu-item {
    background-color: transparent;
    border: none;
    color: white;
    padding: 15px 20px;
    text-align: left;
    font-size: 1.1em;
    cursor: pointer;
    transition: background-color 0.2s ease, color 0.2s ease;
    width: 100%;
    box-sizing: border-box;
}

.sidebar-menu .menu-item:hover {
    background-color: rgba(var(--color-secondary-rgb), 0.2);
    color: var(--color-secondary);
}

.sidebar-menu .menu-item.active {
    background-color: var(--color-secondary);
    color: var(--color-primary);
    font-weight: bold;
}

/* Botón premium especial */
.menu-item-premium {
    background: linear-gradient(135deg, var(--color-secondary), #0cb982) !important;
    color: var(--color-primary) !important;
    margin: 10px 15px;
    border-radius: 12px !important;
    padding: 15px 20px !important;
    display: flex !important;
    align-items: center !important;
    gap: 12px !important;
    box-shadow: 0 4px 15px rgba(14, 218, 145, 0.4);
    border: 2px solid transparent;
    transition: all 0.3s ease !important;
    position: relative;
    overflow: hidden;
}

.menu-item-premium::before {
    content: '';
    position: absolute;
    top: 0;
    left: -100%;
    width: 100%;
    height: 100%;
    background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.3), transparent);
    transition: left 0.6s ease;
}

.menu-item-premium:hover::before {
    left: 100%;
}

.menu-item-premium:hover {
    transform: translateX(5px) !important;
    box-shadow: 0 6px 25px rgba(14, 218, 145, 0.6) !important;
    border-color: rgba(255, 255, 255, 0.3);
}

.menu-item-premium.active {
    background: linear-gradient(135deg, #0cb982, var(--color-secondary)) !important;
    transform: translateX(8px) !important;
    box-shadow: 0 8px 30px rgba(14, 218, 145, 0.7) !important;
}

.premium-icon {
    font-size: 1.3em;
    filter: drop-shadow(0 2px 4px rgba(0, 1, 145, 0.3));
}

.premium-text {
    display: flex;
    flex-direction: column;
    flex: 1;
    gap: 2px;
}

.premium-title {
    font-size: 0.95em;
    font-weight: bold;
    line-height: 1.2;
}

.premium-subtitle {
    font-size: 0.75em;
    opacity: 0.8;
    font-weight: 600;
    text-transform: uppercase;
    letter-spacing: 0.5px;
}

.premium-arrow {
    font-size: 1.2em;
    transition: transform 0.3s ease;
}

.menu-item-premium:hover .premium-arrow {
    transform: translateX(3px);
}

/* Efectos especiales */
@keyframes premium-glow {
    0%, 100% {
        box-shadow: 0 4px 15px rgba(14, 218, 145, 0.4);
    }
    50% {
        box-shadow: 0 4px 25px rgba(14, 218, 145, 0.6);
    }
}

.menu-item-premium:not(.active):not(:hover) {
    animation: premium-glow 3s ease-in-out infinite;
}

.main-content-area {
    flex-grow: 1;
    padding: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    position: relative;
    transition: margin-left var(--transition-speed) var(--transition-ease);
    overflow-y: auto;
}

.toggle-sidebar-btn {
    position: fixed;
    top: 20px;
    left: 20px;
    background-color: var(--color-primary);
    color: white;
    padding: 10px 15px;
    border: none;
    border-radius: 5px;
    font-size: 1.5em;
    cursor: pointer;
    z-index: 10;
    box-shadow: 0 2px 5px rgba(var(--color-text-dark-rgb), 0.2);
    transition: background-color 0.2s ease;
}

.toggle-sidebar-btn:hover {
    background-color: #00007a;
}

.overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 99;
    display: none;
}

.overlay.visible {
    display: block;
}

.container {
    background-color: #fff;
    padding: 30px;
    border-radius: 8px;
    box-shadow: 0 2px 10px rgba(var(--color-text-dark-rgb, 0, 0, 0), 0.1);
    text-align: center; /* Centra el texto y elementos inline dentro del contenedor */
    width: 90%;
    max-width: 600px;
    position: relative;
    z-index: 1;
}

h1, h2 {
    color: var(--color-primary);
    margin-bottom: 20px; /* Ajuste para h1 y h2 */
}
h2 {
    font-size: 1.8em;
    margin-bottom: 15px; /* Sobrescribe para h2 */
}
p {
    color: var(--color-text-gray);
    margin-bottom: 10px;
}

.button-container {
    display: flex;
    justify-content: center;
    gap: 10px;
    margin-bottom: 20px;
    flex-wrap: wrap;
}

button {
    padding: 10px 20px;
    border: none;
    border-radius: 5px;
    font-size: 1em;
    cursor: pointer;
    transition: background-color var(--transition-speed) var(--transition-ease), transform var(--transition-fast) var(--transition-ease);
}

button:active {
    transform: scale(0.98);
}

button#ejecutarBtn,
.modal-buttons button.save-button,
#restartModal .modal-buttons button#restartNowBtn,
#versionCheckModal .modal-buttons button.download-button {
    background-color: var(--color-secondary);
    color: var(--color-primary);
    box-shadow: 0 2px 5px rgba(var(--color-secondary-rgb), 0.3); /* Sombra para el botón de guardar */
}
button#ejecutarBtn:hover,
.modal-buttons button.save-button:hover,
#restartModal .modal-buttons button#restartNowBtn:hover,
#versionCheckModal .modal-buttons button.download-button:hover {
    background-color: #0cb982; /* Tono más oscuro de verde secundario */
    box-shadow: 0 4px 8px rgba(var(--color-secondary-rgb), 0.4); /* Sombra más pronunciada en hover */
}

button#cerrarBtn,
.remove-file-btn,
.predetermined-group-item .group-actions .delete-group-btn {
    background-color: #dc3545;
    color: white;
}
button#cerrarBtn:hover,
.remove-file-btn:hover,
.predetermined-group-item .group-actions .delete-group-btn:hover {
    background-color: #c82333;
}

.modal-buttons button.close-button,
#restartModal .modal-buttons button#cancelRestartBtn,
#versionCheckModal .modal-buttons button.close-button {
    background-color: #6c757d;
    color: white;
}
.modal-buttons button.close-button:hover,
#restartModal .modal-buttons button#cancelRestartBtn:hover,
#versionCheckModal .modal-buttons button.close-button:hover {
    background-color: #5a6268;
}

button#reiniciarBtn {
    background-color: #ffc107;
    color: var(--color-text-dark);
}
button#reiniciarBtn:hover {
    background-color: #e0a800;
}

button#contextoBtn {
    background-color: var(--color-primary);
    color: white;
}
button#contextoBtn:hover {
    background-color: #00007a;
}

button#editKeyBtn {
    background-color: #6f42c1;
    color: white;
}
button#editKeyBtn:hover {
    background-color: #563d7c;
}

.donate-button {
    position: fixed;
    top: 20px;
    right: 20px;
    background-color: var(--color-secondary);
    color: var(--color-primary);
    padding: 15px 30px;
    border: none;
    border-radius: 8px;
    font-size: 1.2em;
    cursor: pointer;
    transition: background-color var(--transition-speed) var(--transition-ease), box-shadow var(--transition-speed) var(--transition-ease), transform var(--transition-fast) var(--transition-ease);
    z-index: 1001;
    box-shadow: 0 0 10px var(--color-secondary);
    text-shadow: 0 0 5px rgba(var(--color-primary-rgb), 0.5);
}

.donate-button:hover {
    background-color: #0cb982;
    box-shadow: 0 0 20px var(--color-secondary), 0 0 30px var(--color-secondary), 0 0 40px var(--color-secondary);
}

#resultado {
    margin-top: 20px;
    padding: 15px;
    background-color: var(--color-accent2);
    border-radius: 5px;
    text-align: center;
    min-height: 1.5em;
    overflow-y: auto;
    max-height: 300px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

#resultado canvas {
    max-width: 100%;
    height: auto;
    margin-bottom: 10px;
}

#resultado .status-text {
    text-align: left;
    width: 100%;
    margin-bottom: 10px;
    color: var(--color-text-dark);
}

.modal-backdrop {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(var(--color-text-dark-rgb, 0, 0, 0), 0.6);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000;
    visibility: hidden;
    opacity: 0;
    transition: visibility 0s, opacity var(--transition-speed) var(--transition-ease);
    pointer-events: none;
}

.modal-backdrop.show {
    visibility: visible;
    opacity: 1;
    pointer-events: auto;
}

.modal-content {
    background-color: var(--color-bg-light);
    padding: 20px;
    border-radius: 8px;
    box-shadow: 0 4px 20px rgba(var(--color-text-dark-rgb, 0, 0, 0), 0.2);
    width: 90%;
    max-width: 500px;
    position: relative;
    color: var(--color-text-dark); /* Color de texto principal para el contenido del modal */
}

.modal-content h2 {
    margin-top: 0;
    color: var(--color-text-dark);
    border-bottom: 1px solid var(--color-accent1);
    padding-bottom: 10px;
    margin-bottom: 15px;
}

.modal-content textarea,
.modal-content input[type="text"] {
    width: 100%;
    padding: 10px;
    border: 1px solid var(--color-accent1);
    border-radius: 4px;
    font-size: 1em;
    margin-bottom: 15px; /* Ajustado para ser consistente */
    box-sizing: border-box;
    background-color: #fff;
    color: var(--color-text-dark);
}

.modal-content textarea {
    height: 150px;
    resize: vertical;
}

.modal-buttons {
    text-align: right;
}

.modal-buttons button {
    padding: 8px 15px;
    border-radius: 4px;
    cursor: pointer;
    transition: background-color var(--transition-speed) var(--transition-ease);
    margin-left: 10px;
    border: none; /* Asegurar que no tengan borde */
}

#keyDescription {
    font-size: 0.9em;
    color: var(--color-text-gray);
    margin-bottom: 15px;
    text-align: left;
}
#keyDescription a {
    color: var(--color-primary);
    text-decoration: none;
}
#keyDescription a:hover {
    text-decoration: underline;
}

#restartModal .modal-content {
    max-width: 400px;
    text-align: center;
}
#restartModal .modal-content h2 {
    border-bottom: none;
    margin-bottom: 10px;
}
#restartModal .modal-content p {
    margin-bottom: 20px;
}
#restartModal .modal-buttons {
    text-align: center;
}
#restartModal .modal-buttons button {
    margin: 0 5px;
}

#versionCheckModal .modal-content {
    max-width: 450px;
    text-align: center;
}
#versionCheckModal .modal-content h2 {
    border-bottom: none;
    margin-bottom: 15px;
}
#versionCheckModal .modal-content p {
    margin-bottom: 20px;
    font-size: 1.1em;
}
#versionCheckModal .modal-content a {
    color: var(--color-primary);
    text-decoration: none;
    font-weight: bold;
    display: block;
    margin-top: 15px;
}
#versionCheckModal .modal-content a:hover {
    text-decoration: underline;
}

#versionCheckModal .modal-buttons {
    text-align: center;
    margin-top: 20px;
}
#versionCheckModal .modal-buttons button {
    margin: 0 5px;
}
#versionCheckModal .modal-buttons button.ok-button {
    background-color: #007bff;
    color: white;
}
#versionCheckModal .modal-buttons button.ok-button:hover {
    background-color: #0056b3;
}

.content-section {
    display: none;
    width: 100%;
    height: 100%;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.content-section.active {
    display: flex;
}

.whatsapp-status-indicator {
    position: fixed;
    bottom: 20px;
    right: 20px;
    background-color: rgba(var(--color-text-dark-rgb), 0.8);
    color: white;
    padding: 10px 15px;
    border-radius: 20px;
    display: flex;
    align-items: center;
    gap: 8px;
    font-size: 0.9em;
    box-shadow: 0 2px 10px rgba(var(--color-text-dark-rgb), 0.3);
    z-index: 100;
}

.whatsapp-status-indicator .status-light {
    width: 12px;
    height: 12px;
    border-radius: 50%;
    background-color: gray;
    transition: background-color var(--transition-speed) var(--transition-ease);
}

.whatsapp-status-indicator .status-light.active {
    background-color: var(--color-secondary);
    box-shadow: 0 0 8px var(--color-secondary);
}

.whatsapp-status-indicator .status-light.inactive {
    background-color: #6c757d;
}

/* Responsive para móviles */
@media (max-width: 768px) {
    .container {
        padding: 20px;
        margin-top: 0;
    }
    h1 {
        font-size: 2em;
    }
    .button-container {
        flex-direction: column;
        gap: 10px;
    }
    button {
        width: 100%;
    }
    .donate-button {
        position: static;
        margin-bottom: 20px;
        width: 100%;
        text-align: center;
        padding: 12px 20px;
        font-size: 1em;
        box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
        text-shadow: none;
    }
    .donate-button:hover {
        box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    }
    .modal-content {
        padding: 20px;
    }
    .modal-content h2 {
        font-size: 1.5em;
    }
    .modal-buttons {
        text-align: center;
    }

    .sidebar {
        width: 200px;
    }
    .toggle-sidebar-btn {
        top: 10px;
        left: 10px;
        font-size: 1.2em;
        padding: 8px 12px;
    }
    .main-content-area {
        padding: 10px;
    }

    .whatsapp-status-indicator {
        bottom: 10px;
        right: 10px;
        padding: 8px 12px;
        font-size: 0.8em;
    }
    .whatsapp-status-indicator .status-light {
        width: 10px;
        height: 10px;
    }

    /* Responsive del menú premium para móviles */
    .menu-item-premium {
        margin: 8px 10px;
        padding: 12px 15px !important;
        gap: 10px !important;
    }
    
    .premium-icon {
        font-size: 1.1em;
    }
    
    .premium-title {
        font-size: 0.9em;
    }
    
    .premium-subtitle {
        font-size: 0.7em;
    }
    
    .premium-arrow {
        font-size: 1em;
    }
}

/* Pon esto en tu archivo de estilos principal (ej: App.css) */

/* Añadir estos estilos a tu App.css o crear un archivo separado */

/* Estilos para los botones del menú con iconos */
.menu-item {
  display: flex;
  align-items: center;
  gap: 12px;
  width: 100%;
  padding: 12px 20px;
  border: none;
  background: transparent;
  color: inherit;
  text-align: left;
  cursor: pointer;
  transition: all 0.3s ease;
  font-size: 14px;
  position: relative;
}

/* Los iconos SVG heredan el color del texto del botón */
.menu-item svg {
  flex-shrink: 0;
  color: currentColor;
  transition: transform 0.2s ease;
}

.menu-item:hover {
  background-color: rgba(255, 255, 255, 0.1);
}

.menu-item:hover svg {
  transform: translateX(2px);
}

.menu-item.active {
  background-color: rgba(255, 255, 255, 0.2);
  font-weight: 600;
}

.menu-item.active svg {
  transform: scale(1.1);
}

/* Ajuste especial para el botón premium */
.menu-item-premium {
  gap: 8px;
}

.menu-item-premium .premium-icon {
  flex-shrink: 0;
}

.menu-item-premium .premium-text {
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 2px;
}

.menu-item-premium .premium-title {
  font-weight: 600;
}

.menu-item-premium .premium-subtitle {
  font-size: 12px;
  opacity: 0.8;
}

.menu-item-premium .premium-arrow {
  margin-left: auto;
  opacity: 0.7;
  transition: transform 0.2s ease;
}

.menu-item-premium:hover .premium-arrow {
  transform: translateX(3px);
  opacity: 1;
}

/* Animación sutil para los iconos al hacer clic */
.menu-item:active svg {
  transform: scale(0.95);
}

/* Si tienes un tema oscuro, los iconos se adaptarán automáticamente */
.sidebar {
  color: #333; /* o el color que uses para el texto */
}

/* Para tema oscuro */
.sidebar.dark {
  color: #fff;
}

/* Responsivo: ajustar tamaño de iconos en pantallas pequeñas */
@media (max-width: 768px) {
  .menu-item svg {
    width: 18px;
    height: 18px;
  }
  
  .menu-item {
    padding: 10px 16px;
    font-size: 13px;
    gap: 10px;
  }
}

/*
 * =======================================================
 * ESTILO DE SCROLLBAR PERSONALIZADO PARA ELECTRON (CHROMIUM)
 * =======================================================
 */

/* Aplica el estilo solo al contenedor del menú que tiene el scroll */
.menu-items-top::-webkit-scrollbar {
  width: 8px; /* Un ancho delgado y minimalista */
}

/* El fondo o "riel" del scrollbar */
.menu-items-top::-webkit-scrollbar-track {
  background: transparent; /* Hacemos el fondo invisible para que no estorbe */
  margin: 5px 0; /* Un pequeño margen vertical para que no toque los bordes */
}

/* La barra deslizante (el "pulgar") */
.menu-items-top::-webkit-scrollbar-thumb {
  background-color: var(--color-accent1); /* Usamos tu azul pálido para un buen contraste */
  border-radius: 20px; /* Bordes totalmente redondeados para un look moderno */
  
  /* Este truco hace que la barra parezca más delgada y flotante */
  border: 2px solid var(--color-primary); 
}

/* El estilo de la barra cuando el cursor está encima */
.menu-items-top::-webkit-scrollbar-thumb:hover {
  background-color: var(--color-secondary); /* Cambia a tu color verde de acento */
}