.clientes-container {
  padding: 20px 10%;
  max-width: 1200px;
  margin: 0 auto;
  background-color: var(--color-bg-light);
  min-height: 100vh;
  padding-top: calc(320px + 10%); /* Espacio de 300px + 20px + 10% adicional */
}

.clientes-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 30px;
  padding: 20px;
  background: white;
  border-radius: 12px;
  box-shadow: 0 2px 10px rgba(var(--color-text-dark-rgb), 0.1);
}

.clientes-header h1 {
  color: var(--color-primary);
  margin: 0;
  font-size: 28px;
  font-weight: 700;
}

.agente-info {
  display: flex;
  gap: 15px;
  align-items: center;
}

.agente-badge {
  background: var(--color-primary);
  color: white;
  padding: 8px 16px;
  border-radius: 20px;
  font-weight: 600;
  font-size: 14px;
}

.clientes-count {
  color: var(--color-text-gray);
  font-size: 14px;
  background: var(--color-accent2);
  padding: 6px 12px;
  border-radius: 15px;
}

.clientes-content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 30px;
}

/* Lista de clientes */
.clientes-lista {
  background: white;
  border-radius: 12px;
  padding: 25px;
  box-shadow: 0 2px 10px rgba(var(--color-text-dark-rgb), 0.1);
}

.clientes-lista h2 {
  color: var(--color-primary);
  margin: 0 0 20px 0;
  font-size: 22px;
  font-weight: 600;
}

.no-clientes {
  text-align: center;
  padding: 40px 20px;
  color: var(--color-text-gray);
}

.lista-items {
  display: flex;
  flex-direction: column;
  gap: 12px;
  max-height: 500px;
  overflow-y: auto;
}

.cliente-item {
  border: 2px solid var(--color-accent2);
  border-radius: 10px;
  padding: 15px;
  cursor: pointer;
  transition: all 0.3s ease;
  background: white;
}

.cliente-item:hover {
  border-color: var(--color-secondary);
  transform: translateY(-1px);
  box-shadow: 0 4px 12px rgba(var(--color-secondary-rgb), 0.2);
}

.cliente-item.selected {
  border-color: var(--color-primary);
  background: linear-gradient(135deg, var(--color-accent1), white);
  box-shadow: 0 4px 15px rgba(var(--color-primary-rgb), 0.2);
}

.cliente-item.inactivo {
  opacity: 0.6;
  background: #f5f5f5;
}

.cliente-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
}

.cliente-numero {
  font-family: 'Courier New', monospace;
  font-weight: bold;
  color: var(--color-primary);
  font-size: 14px;
}

.toggle-activo {
  width: 28px;
  height: 28px;
  border-radius: 50%;
  border: none;
  font-weight: bold;
  cursor: pointer;
  transition: all 0.3s ease;
  font-size: 12px;
}

.toggle-activo.activo {
  background: var(--color-secondary);
  color: white;
}

.toggle-activo.inactivo {
  background: #e74c3c;
  color: white;
}

.toggle-activo:hover {
  transform: scale(1.1);
}

.cliente-detalles {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 10px;
}

.cliente-nombre {
  font-weight: 600;
  color: var(--color-text-dark);
  flex: 1;
}

.cliente-etiqueta {
  padding: 4px 10px;
  border-radius: 15px;
  font-size: 12px;
  font-weight: 500;
  color: var(--color-text-dark);
  white-space: nowrap;
}

/* Editor de clientes */
.clientes-editor {
  background: white;
  border-radius: 12px;
  padding: 25px;
  box-shadow: 0 2px 10px rgba(var(--color-text-dark-rgb), 0.1);
}

.clientes-editor h2 {
  color: var(--color-primary);
  margin: 0 0 20px 0;
  font-size: 22px;
  font-weight: 600;
}

.cliente-seleccionado {
  background: var(--color-accent1);
  padding: 12px 16px;
  border-radius: 8px;
  margin-bottom: 20px;
  color: var(--color-primary);
  font-weight: 600;
}

.editor-form {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.form-group {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.form-group label {
  font-weight: 600;
  color: var(--color-text-dark);
  font-size: 14px;
}

.form-group input,
.form-group select {
  padding: 12px;
  border: 2px solid var(--color-accent2);
  border-radius: 8px;
  font-size: 16px;
  transition: border-color 0.3s ease;
}

.form-group input:focus,
.form-group select:focus {
  outline: none;
  border-color: var(--color-primary);
  box-shadow: 0 0 0 3px rgba(var(--color-primary-rgb), 0.1);
}

.color-selector {
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  gap: 8px;
  margin-bottom: 10px;
}

.color-option {
  width: 40px;
  height: 40px;
  border-radius: 8px;
  border: 3px solid transparent;
  cursor: pointer;
  transition: all 0.3s ease;
}

.color-option:hover {
  transform: scale(1.1);
}

.color-option.selected {
  border-color: var(--color-primary);
  transform: scale(1.15);
  box-shadow: 0 2px 8px rgba(var(--color-primary-rgb), 0.3);
}

.color-input {
  width: 60px;
  height: 40px;
  padding: 0;
  border: 2px solid var(--color-accent2);
  border-radius: 8px;
  cursor: pointer;
}

.checkbox-label {
  display: flex;
  align-items: center;
  gap: 10px;
  cursor: pointer;
  font-weight: 600;
  color: var(--color-text-dark);
}

.checkbox-label input[type="checkbox"] {
  width: 18px;
  height: 18px;
  accent-color: var(--color-secondary);
}

.preview-etiqueta {
  display: flex;
  align-items: center;
  gap: 10px;
  padding: 15px;
  background: var(--color-bg-light);
  border-radius: 8px;
  border: 1px solid var(--color-accent2);
}

.etiqueta-preview {
  padding: 4px 10px;
  border-radius: 15px;
  font-size: 12px;
  font-weight: 500;
  color: var(--color-text-dark);
}

/* Controles superiores */
.controles-superiores {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
  gap: 20px;
}

.busqueda-filtros {
  display: flex;
  gap: 15px;
  flex: 1;
}

.input-busqueda {
  flex: 1;
  padding: 12px 16px;
  border: 2px solid var(--color-accent2);
  border-radius: 25px;
  font-size: 16px;
  transition: all 0.3s ease;
}

.input-busqueda:focus {
  outline: none;
  border-color: var(--color-primary);
  box-shadow: 0 0 0 3px rgba(var(--color-primary-rgb), 0.1);
}

.filtro-estado {
  padding: 12px 16px;
  border: 2px solid var(--color-accent2);
  border-radius: 8px;
  background: white;
  font-size: 14px;
  min-width: 150px;
  cursor: pointer;
  transition: border-color 0.3s ease;
}

.filtro-estado:focus {
  outline: none;
  border-color: var(--color-primary);
}

.btn-agregar-cliente {
  background: linear-gradient(135deg, var(--color-secondary), #0bc77e);
  color: white;
  border: none;
  padding: 12px 20px;
  border-radius: 8px;
  font-size: 14px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  white-space: nowrap;
}

.btn-agregar-cliente:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 15px rgba(var(--color-secondary-rgb), 0.3);
}

/* Etiqueta input group */
.etiqueta-input-group {
  display: flex;
  gap: 8px;
  align-items: center;
  margin-bottom: 10px;
}

.etiqueta-input-group input {
  flex: 1;
  padding: 12px;
  border: 2px solid var(--color-accent2);
  border-radius: 8px;
  font-size: 16px;
  transition: border-color 0.3s ease;
}

.etiqueta-input-group input:focus {
  outline: none;
  border-color: var(--color-primary);
  box-shadow: 0 0 0 3px rgba(var(--color-primary-rgb), 0.1);
}

/* Etiquetas predefinidas */
.etiquetas-predefinidas {
  display: flex;
  flex-wrap: wrap;
  gap: 6px;
  margin-bottom: 10px;
}

.etiqueta-predefinida {
  padding: 4px 8px;
  font-size: 12px;
  border: 1px solid var(--color-accent2);
  border-radius: 12px;
  background: white;
  cursor: pointer;
  transition: all 0.2s ease;
  color: var(--color-text-dark);
}

.etiqueta-predefinida:hover {
  background: var(--color-accent1);
  border-color: var(--color-primary);
  transform: translateY(-1px);
}

.btn-emoji {
  padding: 8px 12px;
  border: 2px solid var(--color-accent2);
  border-radius: 6px;
  background: white;
  cursor: pointer;
  font-size: 16px;
  transition: all 0.3s ease;
}

.btn-emoji:hover {
  border-color: var(--color-secondary);
  background: var(--color-accent1);
}

/* Emoji picker */
.emoji-picker {
  display: grid;
  grid-template-columns: repeat(8, 1fr);
  gap: 5px;
  max-height: 120px;
  overflow-y: auto;
  padding: 10px;
  border: 1px solid var(--color-accent2);
  border-radius: 8px;
  background: white;
  margin-top: 8px;
}

.emoji-option {
  padding: 8px;
  border: none;
  border-radius: 6px;
  background: transparent;
  cursor: pointer;
  font-size: 18px;
  transition: all 0.2s ease;
}

.emoji-option:hover {
  background: var(--color-accent1);
  transform: scale(1.2);
}

/* Botones del formulario */
.botones-formulario {
  display: flex;
  gap: 10px;
  margin-top: 20px;
}

.btn-cancelar {
  background: var(--color-text-gray);
  color: white;
  border: none;
  padding: 15px 25px;
  border-radius: 8px;
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  flex: 1;
}

.btn-cancelar:hover {
  background: #2d3748;
  transform: translateY(-1px);
}

.btn-guardar {
  flex: 2;
}

.btn-guardar {
  background: linear-gradient(135deg, var(--color-secondary), #0bc77e);
  color: white;
  border: none;
  padding: 15px 25px;
  border-radius: 8px;
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  margin-top: 10px;
}

.btn-guardar:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 15px rgba(var(--color-secondary-rgb), 0.3);
}

.btn-guardar:active {
  transform: translateY(0);
}

.no-seleccion {
  text-align: center;
  padding: 40px 20px;
  color: var(--color-text-gray);
  font-style: italic;
}

.loading {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 200px;
  font-size: 18px;
  color: var(--color-text-gray);
}

/* Responsive */
@media (max-width: 768px) {
  .clientes-content {
    grid-template-columns: 1fr;
    gap: 20px;
  }
  
  .clientes-header {
    flex-direction: column;
    gap: 15px;
    text-align: center;
  }

  .controles-superiores {
    flex-direction: column;
    gap: 15px;
  }

  .busqueda-filtros {
    flex-direction: column;
    width: 100%;
  }

  .color-selector {
    grid-template-columns: repeat(4, 1fr);
  }

  .emoji-picker {
    grid-template-columns: repeat(6, 1fr);
  }

  .etiqueta-input-group {
    flex-direction: column;
    align-items: stretch;
  }

  .botones-formulario {
    flex-direction: column;
  }
}