/* src/components/Condiciones.css */

/* Modal principal */
.conditions-modal-optimized {
  max-width: 460px;
  width: 90%;
  height: 645px;
  max-height: 645px;
  position: relative;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  border-radius: 12px;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.15);
}

/* Header compacto */
.conditions-header {
  display: flex;
  align-items: center;
  gap: 12px;
  padding: 16px 20px;
  border-bottom: 1px solid var(--color-accent1);
  background: linear-gradient(135deg, var(--color-bg-light) 0%, #f8f9fa 100%);
  flex-shrink: 0;
}

.header-icon {
  font-size: 1.5em;
  flex-shrink: 0;
}

.header-text {
  flex: 1;
  min-width: 0;
}

.header-text h2 {
  color: var(--color-primary);
  margin: 0 0 4px 0;
  font-size: 1.1em;
  font-weight: 600;
}

.header-text p {
  color: var(--color-text-gray);
  margin: 0;
  font-size: 0.8em;
  line-height: 1.3;
}

/* Contenido principal scrolleable */
.conditions-body {
  flex: 1;
  overflow-y: auto;
  padding: 0;
  display: flex;
  flex-direction: column;
  gap: 0;
}

/* Sección de cuenta */
.account-section {
  padding: 18px 20px;
  border-bottom: 1px solid var(--color-accent1);
  flex-shrink: 0;
}

.input-group {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.input-row {
  display: flex;
}

.form-input {
  width: 100%;
  padding: 10px 12px;
  border: 1px solid var(--color-accent1);
  border-radius: 6px;
  font-size: 0.9em;
  transition: border-color 0.2s ease;
  background: white;
}

.form-input:focus {
  outline: none;
  border-color: var(--color-secondary);
  box-shadow: 0 0 0 2px rgba(13, 202, 240, 0.15);
}

.form-input:disabled {
  background-color: #f8f9fa;
  opacity: 0.6;
  cursor: not-allowed;
}

.form-input::placeholder {
  color: #999;
}

/* Sección de tutorial */
.tutorial-section {
  padding: 18px 20px;
  border-bottom: 1px solid var(--color-accent1);
  flex-shrink: 0;
}

.tutorial-section {
  padding: 18px 20px;
  border-bottom: 1px solid var(--color-accent1);
  flex-shrink: 0;
  text-align: center; /* Añade esto */
}

.tutorial-header {
  display: flex;
  align-items: center;
  align-content: center;
  gap: 8px;
  margin-bottom: 12px;
  justify-content: center; /* Añade esto */
}
.video-container {
  position: relative;
  width: 100%;
  height: 140px;
  border-radius: 6px;
  overflow: hidden;
  background: #f0f0f0;
}

.tutorial-video {
  width: 100%;
  height: 100%;
  border: none;
}

/* Sección de términos */
.terms-section {
  padding: 18px 20px;
  flex-shrink: 0;
}

.terms-checkbox {
  display: flex;
  align-items: flex-start;
  gap: 10px;
}

.checkbox-input {
  width: 18px;
  height: 18px;
  margin-top: 2px;
  cursor: not-allowed;
  opacity: 0.8;
  flex-shrink: 0;
}

.checkbox-input:checked {
  accent-color: var(--color-secondary);
}

.checkbox-label {
  font-size: 0.85em;
  color: var(--color-text-gray);
  line-height: 1.4;
  cursor: pointer;
  user-select: none;
}

.terms-link {
  color: var(--color-secondary);
  text-decoration: underline;
  font-weight: 500;
}

.terms-link:hover {
  color: #0cb982;
}

/* Footer con botones */
.conditions-footer {
  display: flex;
  gap: 12px;
  padding: 16px 20px;
  border-top: 1px solid var(--color-accent1);
  background: #f8f9fa;
  flex-shrink: 0;
}

.btn-secondary, .btn-primary {
  flex: 1;
  padding: 10px 16px;
  border: none;
  border-radius: 6px;
  font-size: 0.85em;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s ease;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 6px;
  min-height: 40px;
}

.btn-secondary {
  background: #6c757d;
  color: white;
}

.btn-secondary:hover {
  background: #5a6268;
  transform: translateY(-1px);
}

.btn-primary {
  background: var(--color-secondary);
  color: var(--color-primary);
  font-weight: 600;
}

.btn-primary:hover:not(:disabled) {
  background: #0cb982;
  transform: translateY(-1px);
}

.btn-primary:disabled {
  background: #ccc;
  color: #666;
  cursor: not-allowed;
  transform: none;
}

.spinner-mini {
  width: 14px;
  height: 14px;
  border: 2px solid transparent;
  border-top: 2px solid var(--color-primary);
  border-radius: 50%;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

/* Nota de advertencia/error */
.warning-note {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  padding: 12px 20px;
  background: #fff3cd;
  border-top: 1px solid #ffc107;
  flex-shrink: 0;
  min-height: 44px;
  transition: background-color 0.3s ease, border-color 0.3s ease;
}

/* Estado de error en la nota */
.warning-note:has(.error-icon) {
  background: #f8d7da;
  border-top-color: #f5c6cb;
}

.warning-icon {
  font-size: 0.9em;
  color: #856404;
}

.warning-text {
  color: #856404;
  font-size: 0.8em;
  font-weight: 500;
}

.error-icon {
  flex-shrink: 0;
  font-size: 0.5em !important;
  color: #721c24;
  line-height: 1;
}

.error-text {
  flex: 1;
  line-height: 1.4;
  color: #721c24;
  font-size: 0.8em;
  font-weight: 500;
}

.error-close {
  background: none;
  border: none;
  color: #721c24;
  font-size: 1.1em;
  font-weight: bold;
  cursor: pointer;
  padding: 2px;
  width: 18px;
  height: 18px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  transition: background-color 0.2s ease;
  margin-left: 8px;
}

.error-close:hover {
  background-color: rgba(114, 28, 36, 0.1);
}

/* Scrollbar personalizada */
.conditions-body::-webkit-scrollbar {
  width: 6px;
}

.conditions-body::-webkit-scrollbar-track {
  background: transparent;
}

.conditions-body::-webkit-scrollbar-thumb {
  background: var(--color-accent1);
  border-radius: 3px;
}

.conditions-body::-webkit-scrollbar-thumb:hover {
  background: #bbb;
}

/* Responsive */
@media (max-width: 480px) {
  .conditions-modal-optimized {
    width: 95%;
    height: 580px;
    max-height: 580px;
  }
  
  .conditions-header {
    padding: 14px 16px;
  }
  
  .header-text h2 {
    font-size: 1.05em;
  }
  
  .header-text p {
    font-size: 0.75em;
  }
  
  .account-section,
  .tutorial-section,
  .terms-section {
    padding: 16px;
  }
  
  .conditions-footer {
    padding: 14px 16px;
    flex-direction: column;
    gap: 8px;
  }
  
  .btn-secondary, .btn-primary {
    flex: none;
  }
  
  .video-container {
    height: 120px;
  }
  
  .form-input {
    padding: 9px 10px;
    font-size: 0.85em;
  }
}

/* Estados de carga */
.loading-state {
  opacity: 0.6;
  pointer-events: none;
}

/* Animaciones de entrada */
.conditions-modal-optimized {
  animation: slideIn 0.3s ease-out;
}

@keyframes slideIn {
  from {
    opacity: 0;
    transform: translateY(-20px) scale(0.95);
  }
  to {
    opacity: 1;
    transform: translateY(0) scale(1);
  }
}

.btn-tutorial-main {
  background: linear-gradient(135deg,#ffffff 0%, #f0f6ff 100%);
  border: 1px solid var(--color-primary);
  color: var(--color-primary);
  font-size: 0.78rem;
  font-weight: 600;
  padding: 11px 24px;
  border-radius: 14px;
  cursor: pointer;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  letter-spacing: .25px;
  box-shadow: 0 4px 14px rgba(0,0,0,0.08);
  position: relative;
  overflow: hidden;
  transition: all .35s ease;
}
.btn-tutorial-main:hover { transform: translateY(-3px); background: linear-gradient(135deg,#ffffff 0%, #e2f3ff 100%); box-shadow:0 8px 20px rgba(0,0,0,0.1); }
.btn-tutorial-main:active { transform: translateY(-1px); }
.btn-tutorial-main:disabled { opacity:.55; cursor:not-allowed; }
