/* src/components/Banner.css */

.banner {
  position: fixed;
  top: 20px;
  left: 50%;
  transform: translateX(-50%);
  width: 500px;
  height: 80px; /* Altura fija - ya la tienes */
  background: linear-gradient(135deg, #000191 0%, #adc9e7 100%);
  border-radius: 12px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 20px;
  color: white;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 4px 20px rgba(0, 1, 145, 0.3);
  z-index: 1000;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  
  /* Agregar estas propiedades para evitar cambios de altura */
  min-height: 80px;
  max-height: 80px;
  box-sizing: border-box;
}

.banner-title {
  font-size: 16px;
  font-weight: 600;
  /* Agregar estas propiedades para controlar el texto */
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 280px; /* Ajustar según necesites */
}

.banner:hover {
  transform: translateX(-50%) translateY(-2px);
  box-shadow: 0 6px 25px rgba(0, 1, 145, 0.4);
}

.banner.loading {
  background: linear-gradient(135deg, #4a5568 0%, #c5dcf0 100%);
  animation: pulse 4s infinite;
  
}

.banner.error {
  background: linear-gradient(135deg, #dc3545 0%, #f8d7da 100%);
}

.banner.warning {
  background: linear-gradient(135deg, #dc3545 0%, #e74c3c 100%);
  
  box-shadow: 0 4px 20px rgba(220, 53, 69, 0.4);
}

.banner.success {
  background: linear-gradient(135deg, #0eda91 0%, #000191 100%);
}

.banner-info {
  flex: 1;
}

.banner-main {
  display: flex;
  align-items: center;
  gap: 10px;
  margin-bottom: 4px;
}

.banner-icon {
  font-size: 18px;
}

.banner-title {
  font-size: 16px;
  font-weight: 600;
}

.banner-sub {
  font-size: 13px;
  opacity: 0.9;
  margin-left: 28px;
}

.banner-action {
  flex-shrink: 0;
}

.banner-button {
  background: rgba(255, 255, 255, 0.2);
  padding: 8px 16px;
  border-radius: 20px;
  font-size: 13px;
  font-weight: 500;
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.3);
  transition: all 0.3s ease;
}

.banner:hover .banner-button {
  background: rgba(255, 255, 255, 0.3);
  transform: scale(1.05);
}

/* Overlay para licencia bloqueada/vencida */
.banner-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: rgba(0, 0, 0, 0.95);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 9999;
  backdrop-filter: blur(10px);
}

.banner-fullscreen {
  background: linear-gradient(135deg, var(--color-primary) 0%, #dc3545 50%, var(--color-primary) 100%);
  border-radius: 20px;
  max-width: 800px;
  width: 90%;
  height: 900px;
  max-height: 90vh;
  box-shadow: 0 20px 60px rgba(var(--color-primary-rgb), 0.6);
  overflow-y: auto;
  border: 3px solid var(--color-secondary);
}

.banner-fullscreen.blocked {
  background: linear-gradient(135deg, var(--color-primary) 0%, #dc3545 50%, var(--color-primary) 100%);
}

.banner-fullscreen.expired {
  background: linear-gradient(135deg, #dc3545 0%, var(--color-primary) 100%);
}

.banner-fullscreen-content {
  padding: 60px 40px 120px 40px;
  text-align: center;
  color: white;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  display: flex;
  flex-direction: column;
  justify-content: center;
  min-height: calc(100% - 120px);
  position: relative;
}

.banner-fullscreen-icon {
  font-size: 80px;
  margin-bottom: 30px;
}

.banner-fullscreen-title {
  font-size: 48px;
  font-weight: 700;
  margin: 0 0 20px 0;
  color: white;
  text-shadow: 0 3px 6px rgba(0, 0, 0, 0.5);
  line-height: 1.2;
}

.banner-fullscreen-subtitle {
  font-size: 24px;
  margin: 0 0 40px 0;
  color: white;
  opacity: 0.95;
  font-weight: 500;
}

.banner-fullscreen-message {
  font-size: 18px;
  line-height: 1.8;
  margin: 0 0 50px 0;
  color: white;
  opacity: 0.95;
  max-width: 600px;
  margin-left: auto;
  margin-right: auto;
  text-align: left;
  background: rgba(var(--color-accent2), 0.2);
  padding: 30px;
  border-radius: 15px;
  border-left: 5px solid var(--color-secondary);
  backdrop-filter: blur(10px);
}

.banner-fullscreen-message p {
  margin: 0 0 15px 0;
  font-weight: 400;
  color: white;
}

.banner-fullscreen-message p:last-child {
  margin-bottom: 0;
}

.banner-fullscreen-buttons {
  display: flex;
  gap: 20px;
  justify-content: center;
  align-items: center;
  position: fixed;
  bottom: 30px;
  left: 50%;
  transform: translateX(-50%);
  z-index: 10000;
}

.banner-fullscreen-button {
  border: none;
  padding: 20px 40px;
  border-radius: 30px;
  font-size: 20px;
  font-weight: 700;
  cursor: pointer;
  transition: all 0.3s ease;
  text-transform: uppercase;
  letter-spacing: 1px;
  min-width: 200px;
}

.banner-fullscreen-button.primary {
  background: linear-gradient(135deg, var(--color-secondary) 0%, var(--color-accent1) 100%);
  color: var(--color-text-dark);
  box-shadow: 0 6px 20px rgba(var(--color-secondary-rgb), 0.4);
  border: 2px solid var(--color-primary);
}

.banner-fullscreen-button.primary:hover {
  transform: translateY(-3px);
  box-shadow: 0 8px 25px rgba(var(--color-secondary-rgb), 0.6);
  background: linear-gradient(135deg, var(--color-accent1) 0%, var(--color-secondary) 100%);
}

.banner-fullscreen-button.secondary {
  background: transparent;
  color: white;
  box-shadow: 0 6px 20px rgba(0, 0, 0, 0.3);
  border: 2px solid rgba(255, 255, 255, 0.5);
}

.banner-fullscreen-button.secondary:hover {
  transform: translateY(-3px);
  background: rgba(255, 255, 255, 0.1);
  border-color: white;
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.4);
}

@keyframes blockPulse {
  /* Animación eliminada - sin movimiento */
}

@keyframes blockShake {
  /* Animación eliminada - sin movimiento */
}

/* Responsive para pantalla completa */
@media (max-width: 768px) {
  .banner-fullscreen {
    width: 95%;
    height: 95vh;
    border-radius: 15px;
  }
  
  .banner-fullscreen-content {
    padding: 40px 30px 140px 30px;
  }
  
  .banner-fullscreen-icon {
    font-size: 60px;
  }
  
  .banner-fullscreen-title {
    font-size: 36px;
  }
  
  .banner-fullscreen-subtitle {
    font-size: 20px;
  }
  
  .banner-fullscreen-message {
    font-size: 16px;
    padding: 25px;
  }
  
  .banner-fullscreen-buttons {
    flex-direction: column;
    gap: 15px;
  }
  
  .banner-fullscreen-button {
    padding: 18px 35px;
    font-size: 18px;
    min-width: 250px;
  }
}

@media (max-width: 480px) {
  .banner-fullscreen-content {
    padding: 30px 20px 160px 20px;
  }
  
  .banner-fullscreen-icon {
    font-size: 50px;
  }
  
  .banner-fullscreen-title {
    font-size: 28px;
  }
  
  .banner-fullscreen-subtitle {
    font-size: 18px;
  }
  
  .banner-fullscreen-message {
    font-size: 15px;
    padding: 20px;
  }
  
  .banner-fullscreen-button {
    padding: 15px 30px;
    font-size: 16px;
    min-width: 200px;
  }
}


@keyframes pulse {
  0%, 100% { opacity: 0.8; }
  50% { opacity: 1; }
}

@keyframes glow {
  0%, 100% { box-shadow: 0 4px 20px rgba(220, 53, 69, 0.4); }
  50% { box-shadow: 0 4px 30px rgba(220, 53, 69, 0.7); }
}

/* Responsive */
@media (max-width: 768px) {
  .banner {
    width: 90%;
    max-width: 450px;
    height: 70px;
    padding: 0 16px;
  }
  
  .banner-title {
    font-size: 15px;
  }
  
  .banner-sub {
    font-size: 12px;
  }
  
  .banner-button {
    padding: 6px 12px;
    font-size: 12px;
  }
}

@media (max-width: 480px) {
  .banner {
    width: 95%;
    height: 65px;
    padding: 0 14px;
  }
  
  .banner-title {
    font-size: 14px;
  }
  
  .banner-sub {
    font-size: 11px;
  }
  
  .banner-icon {
    font-size: 16px;
  }
}