/* Remarketing.css */

.remarketing-container {
    padding: 200px 10% 20px 10%;
    max-width: 1200px;
    margin: 0 auto;
    background-color: var(--color-bg-light);
    min-height: 100vh;
    box-sizing: border-box;
    font-family: 'Inter', sans-serif;
}

.remarketing-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 30px;
    padding-bottom: 20px;
    border-bottom: 2px solid var(--color-accent2);
}

.remarketing-header h2 {
    margin: 0;
    color: var(--color-primary);
    font-size: 2.5rem;
    font-weight: 700;
}

.remarketing-status {
    display: flex;
    align-items: center;
    gap: 15px;
}

.status-indicator {
    padding: 12px 20px;
    border-radius: 12px;
    font-weight: 600;
    font-size: 14px;
    box-shadow: 0 4px 12px rgba(var(--color-primary-rgb), 0.2);
}

.status-indicator.active {
    background: linear-gradient(135deg, var(--color-secondary), #0cb982);
    color: var(--color-primary);
    border: 1px solid var(--color-secondary);
}

.status-indicator.inactive {
    background-color: #f8d7da;
    color: #721c24;
    border: 1px solid #f5c6cb;
}

.toggle-btn {
    padding: 12px 24px;
    border: none;
    border-radius: 8px;
    font-weight: 600;
    cursor: pointer;
    transition: all 0.3s ease;
    box-shadow: 0 4px 12px rgba(var(--color-primary-rgb), 0.2);
}

.toggle-btn.start {
    background: linear-gradient(135deg, var(--color-secondary), #0cb982);
    color: var(--color-primary);
}

.toggle-btn.start:hover {
    transform: translateY(-2px);
    box-shadow: 0 6px 20px rgba(var(--color-secondary-rgb), 0.4);
}

.toggle-btn.stop {
    background: linear-gradient(135deg, #ff4757, #ff3742);
    color: white;
}

.toggle-btn.stop:hover {
    transform: translateY(-2px);
    box-shadow: 0 6px 20px rgba(255, 71, 87, 0.4);
}

.toggle-btn:disabled {
    opacity: 0.6;
    cursor: not-allowed;
}

.mensaje {
    padding: 15px 20px;
    border-radius: 8px;
    margin-bottom: 20px;
    font-weight: 500;
    animation: slideInDown 0.3s ease;
}

.mensaje.success {
    background-color: #d4edda;
    color: #155724;
    border: 1px solid #c3e6cb;
}

.mensaje.error {
    background-color: #f8d7da;
    color: #721c24;
    border: 1px solid #f5c6cb;
}

.remarketing-form {
    display: flex;
    flex-direction: column;
    gap: 30px;
}

.form-section {
    background: white;
    padding: 25px;
    border-radius: 15px;
    box-shadow: 0 4px 12px rgba(var(--color-text-dark-rgb), 0.08);
    border: 1px solid var(--color-accent2);
}

.form-section h3 {
    margin-top: 0;
    margin-bottom: 20px;
    color: var(--color-primary);
    font-size: 1.3rem;
    font-weight: 600;
    border-bottom: 2px solid var(--color-accent2);
    padding-bottom: 10px;
}

.form-group {
    margin-bottom: 20px;
}

.form-group label {
    display: block;
    margin-bottom: 8px;
    font-weight: 600;
    color: var(--color-text-dark);
    font-size: 1.1rem;
}

.form-group input[type="text"],
.form-group input[type="number"],
.form-group input[type="time"],
.form-group textarea {
    width: 100%;
    padding: 15px;
    border: 2px solid var(--color-accent2);
    border-radius: 12px;
    font-size: 1rem;
    font-family: inherit;
    transition: border-color 0.3s ease, box-shadow 0.3s ease;
    background-color: white;
    color: var(--color-text-dark);
}

.form-group input[type="text"]:focus,
.form-group input[type="number"]:focus,
.form-group input[type="time"]:focus,
.form-group textarea:focus {
    outline: none;
    border-color: #007bff;
    box-shadow: 0 0 0 3px rgba(0, 123, 255, 0.1);
}

.form-group input[type="checkbox"] {
    margin-right: 8px;
    transform: scale(1.2);
}

.form-group small {
    display: block;
    margin-top: 5px;
    color: #6c757d;
    font-size: 12px;
}

.horario-group {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 20px;
}

.rango-tiempo-container {
    display: flex;
    align-items: center;
    gap: 15px;
    flex-wrap: wrap;
    justify-content: flex-start;
}

.tiempo-input-group {
    display: flex;
    align-items: center;
    gap: 8px;
    background: white;
    padding: 8px 12px;
    border: 2px solid var(--color-accent2);
    border-radius: 12px;
    transition: all 0.3s ease;
}

.tiempo-input-group:focus-within {
    border-color: var(--color-primary);
    box-shadow: 0 0 0 3px rgba(var(--color-primary-rgb), 0.1);
}

.tiempo-input-group input[type="number"] {
    width: 60px;
    border: none;
    outline: none;
    padding: 0;
    margin: 0;
    background: transparent;
    font-size: 1.1rem;
    font-weight: 600;
    color: var(--color-text-dark);
    text-align: center;
}

.tiempo-input-group input[type="number"]::-webkit-outer-spin-button,
.tiempo-input-group input[type="number"]::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}

.tiempo-input-group input[type="number"] {
    -moz-appearance: textfield;
    appearance: textfield;
}

.tiempo-label {
    font-size: 0.9rem;
    color: var(--color-text-gray);
    font-weight: 500;
    white-space: nowrap;
}

.tiempo-separador {
    font-size: 1.5rem;
    font-weight: bold;
    color: var(--color-primary);
    margin: 0 5px;
}

.form-group input[type="file"] {
    width: 100%;
    padding: 15px;
    border: 2px dashed var(--color-accent2);
    border-radius: 12px;
    background-color: var(--color-bg-light);
    cursor: pointer;
    font-family: inherit;
    transition: all 0.3s ease;
    color: var(--color-text-dark);
}

.form-group input[type="file"]:hover {
    border-color: var(--color-primary);
    background-color: white;
}

.archivos-lista {
    margin-top: 15px;
    padding: 15px;
    background-color: var(--color-bg-light);
    border-radius: 12px;
    border: 1px solid var(--color-accent2);
}

.archivos-lista h4 {
    margin: 0 0 15px 0;
    color: var(--color-primary);
    font-size: 1rem;
    font-weight: 600;
}

.archivo-item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 12px 15px;
    background: white;
    border: 1px solid var(--color-accent2);
    border-radius: 8px;
    margin-bottom: 8px;
    transition: all 0.3s ease;
}

.archivo-item:hover {
    border-color: var(--color-primary);
    transform: translateY(-1px);
    box-shadow: 0 4px 12px rgba(var(--color-primary-rgb), 0.1);
}

.archivo-item:last-child {
    margin-bottom: 0;
}

.remove-btn {
    background: #ff4757;
    color: white;
    border: none;
    border-radius: 50%;
    width: 28px;
    height: 28px;
    cursor: pointer;
    font-weight: bold;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 14px;
    transition: all 0.3s ease;
}

.remove-btn:hover {
    background: #ff3742;
    transform: scale(1.1);
}

.archivos-existentes {
    display: grid;
    gap: 12px;
}

.archivo-existente {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 15px;
    background-color: var(--color-accent2);
    border-radius: 8px;
    border: 1px solid var(--color-accent1);
}

.archivo-tipo {
    font-size: 0.8rem;
    color: var(--color-text-gray);
    background: white;
    padding: 4px 8px;
    border-radius: 12px;
    font-weight: 500;
}

.form-actions {
    display: flex;
    gap: 15px;
    justify-content: flex-end;
    padding: 25px;
    background: white;
    border-radius: 15px;
    box-shadow: 0 4px 12px rgba(var(--color-text-dark-rgb), 0.08);
    border: 1px solid var(--color-accent2);
}

.save-btn, .reload-btn {
    padding: 15px 30px;
    border: none;
    border-radius: 12px;
    font-weight: 600;
    cursor: pointer;
    transition: all 0.3s ease;
    font-size: 1.1rem;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 10px;
    min-height: 50px;
}

.save-btn {
    background: linear-gradient(135deg, var(--color-secondary), var(--color-primary));
    color: white;
    box-shadow: 0 4px 15px rgba(var(--color-secondary-rgb), 0.3);
}

.save-btn:hover:not(:disabled) {
    transform: translateY(-2px);
    box-shadow: 0 6px 25px rgba(var(--color-secondary-rgb), 0.4);
}

.save-btn:disabled {
    opacity: 0.7;
    cursor: not-allowed;
}

.reload-btn {
    background: linear-gradient(135deg, var(--color-accent1), var(--color-accent2));
    color: var(--color-primary);
    border: 2px solid var(--color-accent2);
}

.reload-btn:hover {
    transform: translateY(-2px);
    box-shadow: 0 6px 20px rgba(var(--color-primary-rgb), 0.2);
    border-color: var(--color-primary);
}

.info-section {
    background: linear-gradient(135deg, var(--color-accent1), var(--color-accent2));
    border-left: 4px solid var(--color-primary);
    border-radius: 15px;
}

.info-section h3 {
    color: var(--color-primary);
    margin: 0 0 15px 0;
    font-size: 1.2rem;
    border-bottom: none;
    padding-bottom: 0;
}

.info-section ul {
    margin: 0;
    padding-left: 20px;
}

.info-section li {
    margin-bottom: 8px;
    color: var(--color-text-dark);
    line-height: 1.5;
}

/* Animaciones */
@keyframes slideInDown {
    from {
        transform: translateY(-20px);
        opacity: 0;
    }
    to {
        transform: translateY(0);
        opacity: 1;
    }
}

@keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
}

/* Loading spinner */
.btn-spinner {
    width: 20px;
    height: 20px;
    border: 2px solid rgba(255, 255, 255, 0.3);
    border-top: 2px solid white;
    border-radius: 50%;
    animation: spin 1s linear infinite;
}

/* Responsive */
@media (max-width: 768px) {
    .remarketing-container {
        padding: 250px 5% 15px 5%;
    }
    
    .remarketing-header {
        flex-direction: column;
        gap: 15px;
        text-align: center;
    }
    
    .remarketing-header h2 {
        font-size: 2rem;
    }
    
    .horario-group {
        grid-template-columns: 1fr;
    }
    
    .form-actions {
        flex-direction: column;
    }
    
    .remarketing-status {
        flex-direction: column;
        gap: 10px;
    }
    
    .rango-tiempo-container {
        flex-direction: column;
        align-items: stretch;
        gap: 15px;
    }
    
    .tiempo-input-group {
        justify-content: center;
        padding: 12px 15px;
    }
    
    .tiempo-input-group input[type="number"] {
        width: 80px;
        font-size: 1.2rem;
    }
    
    .tiempo-separador {
        align-self: center;
        font-size: 1.8rem;
    }
    
    .form-section {
        padding: 20px;
    }
    
    .save-btn, .reload-btn {
        width: 100%;
    }
}

/* Switch Toggle */
.switch {
    position: relative;
    display: inline-block;
    width: 60px;
    height: 34px;
}

.switch input {
    opacity: 0;
    width: 0;
    height: 0;
}

.slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #ccc;
    transition: .4s;
    border-radius: 34px;
}

.slider:before {
    position: absolute;
    content: "";
    height: 26px;
    width: 26px;
    left: 4px;
    bottom: 4px;
    background-color: white;
    transition: .4s;
    border-radius: 50%;
}

input:checked + .slider {
    background: linear-gradient(135deg, var(--color-secondary), #0cb982);
}

input:focus + .slider {
    box-shadow: 0 0 1px var(--color-secondary);
}

input:checked + .slider:before {
    transform: translateX(26px);
}

input:disabled + .slider {
    opacity: 0.6;
    cursor: not-allowed;
}

/* Responsive para el switch */
@media (max-width: 768px) {
    .switch {
        width: 50px;
        height: 28px;
    }
    
    .slider:before {
        height: 20px;
        width: 20px;
        left: 4px;
        bottom: 4px;
    }
    
    input:checked + .slider:before {
        transform: translateX(22px);
    }
}