/* src/components/Tutoriales.css */
.tutoriales-container {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  position: relative;
}

.tutoriales-iframe {
  width: 100%;
  height: 100%;
  border: none;
  flex: 1;
  min-height: 600px;
  transition: opacity 0.3s ease;
}

.tutoriales-iframe.loading {
  opacity: 0;
}

/* Preloader */
.tutoriales-preloader {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: var(--color-bg-light);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 10;
}

.preloader-content {
  text-align: center;
  padding: 40px;
}

.spinner {
  width: 60px;
  height: 60px;
  border: 4px solid var(--color-accent1);
  border-top: 4px solid var(--color-primary);
  border-radius: 50%;
  animation: spin 1s linear infinite;
  margin: 0 auto 20px auto;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.preloader-content h3 {
  color: var(--color-primary);
  margin: 0 0 10px 0;
  font-size: 1.5em;
}

.preloader-content p {
  color: var(--color-text-gray);
  margin: 0;
  font-size: 1em;
}

/* Error */
.tutoriales-error {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: var(--color-bg-light);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 10;
}

.error-content {
  text-align: center;
  padding: 40px;
  background: white;
  border-radius: 15px;
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.1);
  max-width: 400px;
  margin: 20px;
}

.error-icon {
  font-size: 4em;
  margin-bottom: 20px;
}

.error-content h3 {
  color: var(--color-primary);
  margin: 0 0 15px 0;
  font-size: 1.4em;
}

.error-content p {
  color: var(--color-text-gray);
  margin: 0 0 25px 0;
  line-height: 1.5;
}

.retry-btn {
  background: var(--color-secondary);
  color: var(--color-primary);
  padding: 12px 25px;
  border: none;
  border-radius: 25px;
  font-size: 1em;
  font-weight: bold;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 4px 15px rgba(14, 218, 145, 0.3);
}

.retry-btn:hover {
  background: #0cb982;
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(14, 218, 145, 0.4);
}

.retry-btn:active {
  transform: translateY(0);
}

/* Animaciones adicionales */
.tutoriales-preloader {
  animation: fadeIn 0.3s ease;
}

.tutoriales-error {
  animation: fadeIn 0.3s ease;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

/* Responsive */
@media (max-width: 768px) {
  .tutoriales-iframe {
    min-height: 500px;
  }
  
  .preloader-content {
    padding: 30px 20px;
  }
  
  .spinner {
    width: 50px;
    height: 50px;
  }
  
  .preloader-content h3 {
    font-size: 1.3em;
  }
  
  .error-content {
    margin: 10px;
    padding: 30px 20px;
  }
  
  .error-icon {
    font-size: 3em;
  }
  
  .error-content h3 {
    font-size: 1.2em;
  }
}

@media (max-width: 480px) {
  .tutoriales-iframe {
    min-height: 400px;
  }
  
  .preloader-content {
    padding: 20px;
  }
  
  .spinner {
    width: 40px;
    height: 40px;
    border-width: 3px;
  }
  
  .preloader-content h3 {
    font-size: 1.2em;
  }
  
  .preloader-content p {
    font-size: 0.9em;
  }
}